@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&family=Ubuntu:wght@400;700&display=swap');

:root{
  --font: 'Ubuntu', sans-serif;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font);
}

/* loader */

.spinner{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.274);
}

.form-btn{
  width: 250px !important;
  margin-top: 30px !important;
}

/* register and login  */

.text{
  color: black;
  text-decoration: none;
}
.text:hover{
  color: black;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}